import React, { useState, useEffect, useCallback } from "react";
import MUIDataTable from "mui-datatables";
import { Avatar } from "@mui/material";
import Header from "./Header";

const CharacterTable = () => {
  const [allcharacters, setCharacters] = useState([]);
  const [species, setSpecies] = useState("");
  const [gender, setGender] = useState("");
  const [status, setStatus] = useState("");
  const [page, setPage] = useState(0);

  const getCharacters = async () => {
    const fetchResponse = await fetch(
      "https://rickandmortyapi.com/api/character/"
    );
    const fullCharacter = await fetchResponse.json();
    setCharacters(fullCharacter.results);
  };

  const filterHandler = useCallback(async () => {
    try {
      const result = await fetch(
        `https://rickandmortyapi.com/api/character/?page=${page}&gender=${gender}&species=${species}&status=${status}`
      );
      const searchedSpecies = await result.json();
      setCharacters([...searchedSpecies.results]);
      console.log();
    } catch (err) {
      console.log("error", err.message);
    }
  }, [gender, species, status, page]);

  useEffect(() => {
    getCharacters();
  }, []);

  useEffect(() => {
    filterHandler();
  }, [gender, species, status, page, filterHandler]);

  const columns = [
    {
      name: "id",
      label: "ID",
      options: {
        filter: true,
        sort: true,
      },
    },
    {
      name: "name",
      label: "Name",
      options: {
        filter: false,
        sort: true,
      },
    },
    {
      name: "image",
      label: "Image",
      options: {
        customBodyRender: (value) => {
          return <Avatar variant="circular" src={value} alt="avatar" />;
        },
        filter: false,
        sort: false,
      },
    },
    {
      name: "species",
      label: "Species",
      options: {
        filter: true,
        sort: false,
        filterType: "dropdown",
      },
    },
    {
      name: "gender",
      label: "Gender",
      options: {
        filter: true,
        sort: false,
        filterType: "textField",
        customFilterValue: (value, filter) => {
          return value.toLowerCase().includes(filter.toLowerCase());
        },
      },
    },
    {
      name: "status",
      label: "Status",
      options: {
        filter: true,
        sort: false,
        filterType: "dropdown",
      },
    },
  ];

  const options = {
    serverSide: true,
    count: 826,
    renderPerPage: 20,
    filter: true,
    confirmFilters: false,
    onTableChange: (action, tableState) => {
      if (action === "changePage") {
        const changePage = tableState.page + 1;
        setPage(changePage);
      }
    },
    onFilterChange: (changedColumn, filterList) => {
      setGender(filterList[4]);
      setSpecies(filterList[3]);
      setStatus(filterList[5]);
    },
  };

  return (
    <div>
      <Header />
      <MUIDataTable
        title="Rick and Morty Characters"
        data={allcharacters}
        columns={columns}
        options={options}
      />
    </div>
  );
};

export default CharacterTable;
